.question-3 {

    .option-image {
        width: 100%;
        height: 100%;
    }

    .question-container {
        text-align: center;
        margin-top: 20px;
      }
      
      .question-container h2{
        text-align: center;
        font-weight: 400;
        color: #484848;
      }
      
      .options {
        display: grid;
        grid-template-rows: 1fr 1fr;
        grid-template-columns: 1fr 1fr;
        gap: 15px;
        margin: 20px 0;
    
        .opt-btn-img {
            height: 250px;
            width: 250px;
            margin: 10px 15px;
        }
      }
      
      .option-btn {
        padding: 20px 35px;
        background-color: #fff;
        border: 1px solid #484848;
        border-radius: 10px;
        cursor: pointer;
        
        span {
            font-size: 28px;
            color: #484848;
        }
    
        &.selected {
            background-color: #f6b52e;
            
            
            span {
                color: #fff;
                font-weight: 900;
            }
        }
      }

      .submit-btn {
        padding: 10px 20px;
        background-color: #3564ab;
        color: #fff;
        text-transform: uppercase;
        border: none;
        border-radius: 10px;
        font-size: 20px;
        cursor: pointer;
        margin-top: 60px;
        width: fit-content;
        margin: 30px auto;

        &:hover {
            background-color: #28a745;
        }
      }
      
      .submit-btn:disabled {
        background-color: #ccc;
        cursor: not-allowed;
      }
    
}